export const getSVG = (name, myWidth) => {
  if (name === "Announcement") {
    return announcement(myWidth);
  } else if (name === "Arts") {
    return arts(myWidth);
  } else if (name === "Book") {
    return book(myWidth);
  } else if (name === "Convo") {
    return convo(myWidth);
  } else if (name === "Food") {
    return food(myWidth);
  } else if (name === "Heart") {
    return heart(myWidth);
  } else if (name === "Industry") {
    return industry(myWidth);
  } else if (name === "Laptop") {
    return laptop(myWidth);
  } else if (name === "LightBulb") {
    return lightBulb(myWidth);
  } else if (name === "Panel") {
    return panel(myWidth);
  } else if (name === "PplConvo") {
    return pplConvo(myWidth);
  } else if (name === "Star") {
    return star(myWidth);
  } else if (name === "TakeNotes") {
    return takeNotes(myWidth);
  }
}
